import EventEmitter from 'component-emitter'
import shortid from 'shortid'
import debug from 'debug'
import sortKeys from 'sort-keys'

import Transport from './transport'
import Subscription from './subscription'
import Channels from './channels/channels'
import Fetch from './fetch'
import * as events from './protocol'
import Timer from './utils/timer'
import on from './utils/on'
import jsonprune from './utils/jsonprune'
import { PermissionError, SubscriptionError } from './errors'

const logger = debug('rapid:connection')

export default class Connection extends EventEmitter {
  constructor(url, opts = {}) {
    super()
    this.transport = new Transport(url, opts)
    this.conId = shortid.generate()
    this.authEvent = null
    this.subscriptions = new Map()
    this.channels = new Channels(this)
    this.fetches = new Map()
    this.writebuffer = new Map()
    this.nooptimer = new Timer(() => this.noop(), opts.noopDelay || 45000)
    this.connected = false
    this.init()
  }

  init() {
    this.subs = [
      on(this.transport, 'event', evt => this.onevent(evt)),
      on(this.transport, 'connect', () => this.onconnect()),
      on(this.transport, 'reconnect', () => this.onreconnect()),
      on(this.transport, 'connected', state => this.emit('connected', state)),
      on(this, 'ack', evt => this.onack(evt)),
      on(this, 'err', evt => this.onerror(evt)),
      on(this, 'batch', evt => this.onbatch(evt)),
      on(this, 'ca', evt => this.oncancel(evt)),
      on(this, 'permission-denied', err => this.onpermissiondenied(err))
    ]
  }

  close() {
    logger('close request')
    this.subs.forEach(sub => sub.off())
    this.nooptimer.stop()
    this.transport.disconnect()
  }

  onevent(evt) {
    const key = Object.keys(evt)[0]
    key !== 'val'
      ? logger('received event %s - %o', key, evt)
      : logger('received event %s', key)
    this.emit(key, evt[key])
    this.nooptimer.reset()
  }

  onconnect() {
    logger('onconnect')
    this.send(events.con(this.conId))
    if (this.authEvent) {
      this.send(this.authEvent)
    }
    this.subscriptions.forEach(sub => {
      sub.subscribed = false
      this.send(sub.evt)
    })
    this.fetches.forEach(ftc => {
      ftc.acked = false
      this.send(ftc.evt)
    })
    this.flush()
    this.channels.connect()
    this.connected = true
  }

  onreconnect() {
    logger('onreconnect')
    if (!this.connected) {
      this.onconnect()
      return
    }
    this.send(events.rec(this.conId))
    this.subscriptions.forEach(sub => {
      if (!sub.subscribed) {
        this.send(sub.evt)
      }
    })
    this.fetches.forEach(ftc => {
      if (!ftc.acked) {
        this.send(ftc.evt)
      }
    })
    this.flush()
    this.channels.reconnect()
  }

  onack(evt) {
    const evtId = evt['evt-id']
    const removed = this.writebuffer.delete(evtId)
    if (removed) {
      return
    }

    for (const [, val] of this.subscriptions) {
      if (val.evt.sub['evt-id'] === evtId) {
        val.subscribed = true
        return
      }
    }

    for (const [, val] of this.fetches) {
      if (val.evt.ftc['evt-id'] === evtId) {
        val.acked = true
        return
      }
    }
  }

  onerror(evt) {
    switch (evt['err-type']) {
      case 'connection-terminated':
        return this.onconnect() // reconnected, but server does not remember anything
      case 'invalid-auth-token':
        return this.emit('invalid-auth-token', evt)
      case 'permission-denied':
        return this.emit('permission-denied', evt)
      case 'etag-conflict':
        return this.emit('etag-conflict', evt)
      default: {
        return logger('unkown server error %o', evt)
      }
    }
  }

  onpermissiondenied(err) {
    const evtId = err['evt-id']

    for (const [key, val] of this.subscriptions) {
      if (val.evt.sub['evt-id'] === evtId) {
        val.cancel(new PermissionError())
        this.subscriptions.delete(key)
        return
      }
    }

    for (const [key, val] of this.fetches) {
      if (val.evt.ftc['evt-id'] === evtId) {
        val.cancel(new PermissionError())
        this.fetches.delete(key)
        return
      }
    }
  }

  oncancel(evt) {
    const evtId = evt['evt-id']
    const subId = evt['sub-id']

    this.acknowledge(evtId)

    for (const [key, val] of this.subscriptions) {
      if (val.id === subId) {
        val.cancel(new SubscriptionError())
        this.subscriptions.delete(key)
      }
    }
  }

  onbatch(batch) {
    for (const evt of batch) {
      if (evt.ca) {
        this.oncancel(evt.ca)
      }
      if (evt.res) {
        this.emit('res', evt.res)
      }
    }
  }

  flush() {
    this.writebuffer.forEach(evt => this.send(evt))
  }

  send(evt) {
    logger('sending event: %o', jsonprune(evt))
    if (!evt.nop) {
      this.nooptimer.reset()
    }
    this.transport.send(evt)
  }

  acknowledge(evtId) {
    const evt = events.ack(evtId)
    this.send(evt)
  }

  noop() {
    const evt = events.nop()
    this.send(evt)
  }

  authorize(authToken) {
    const evt = events.auth(authToken)
    const evtId = evt.auth['evt-id']
    this.authEvent = evt
    this.send(evt)

    return evtId
  }

  deauthorize() {
    const evt = events.deauth()
    const evtId = evt.deauth['evt-id']
    this.authEvent = null
    this.writebuffer.set(evtId, evt)
    this.send(evt)

    return evtId
  }

  subscribe(colId, query, onVal, onErr) {
    query = jsonprune(query)

    const id = JSON.stringify(sortKeys({ colId, query }))
    let subscription

    if (this.subscriptions.has(id)) {
      subscription = this.subscriptions.get(id)
      subscription.addListeners(onVal, onErr)
    } else {
      subscription = new Subscription({ io: this, query, onVal, onErr })
      const evt = events.sub(subscription.id, colId, query)
      subscription.evt = evt
      this.subscriptions.set(id, subscription)
      this.send(evt)
    }

    return () => {
      subscription.unsubscribe(onVal, onErr)
      if (subscription.unsubscribed) {
        this.unsubscribe(subscription.id)
        this.subscriptions.delete(id)
      }
    }
  }

  unsubscribe(subId) {
    const evt = events.uns(subId)
    const evtId = evt['evt-id']

    this.writebuffer.set(evtId, evt)
    this.send(evt)

    return evtId
  }

  fetch(colId, query, onValue, onError) {
    const evt = events.ftc(colId, query)
    const evtId = evt.ftc['evt-id']

    const fetch = new Fetch({ connection: this, evt, onValue, onError })
    this.fetches.set(evtId, fetch)

    this.send(evt)

    return evtId
  }

  mutate(colId, docId, body, etag) {
    const evt = events.mut(colId, docId, body, etag)
    const evtId = evt.mut['evt-id']

    this.writebuffer.set(evtId, evt)
    this.send(evt)

    return evtId
  }

  merge(colId, docId, body, etag) {
    const evt = events.mer(colId, docId, body, etag)
    const evtId = evt.mer['evt-id']

    this.writebuffer.set(evtId, evt)
    this.send(evt)

    return evtId
  }

  delete(colId, docId, etag) {
    const evt = events.del(colId, docId, etag)
    const evtId = evt.del['evt-id']

    this.writebuffer.set(evtId, evt)
    this.send(evt)

    return evtId
  }

  subscribeChannel(chanId, onvalue, onerror) {
    return this.channels.subscribe(chanId, onvalue, onerror)
  }

  publishChannel(chanId, message) {
    return this.channels.publish(chanId, message)
  }

  removeChannel(chanId, onvalue, onerror) {
    return this.channels.remove(chanId, onvalue, onerror)
  }
}
