import EventEmitter from 'component-emitter'
import Backoff from 'backo'
import debug from 'debug'
import { validate } from '@rapid-io/protocol'

import WebSocket from './websocket'
import on from './utils/on'
import jsonparse from './utils/jsonparse'
import jsonprune from './utils/jsonprune'

const logger = debug('rapid:transport')

export default class Transport extends EventEmitter {
  constructor(
    url,
    {
      reconnectDelay = 1000,
      reconnectDelayMax = 5000,
      reconnectAttempts = Infinity
    } = {}
  ) {
    super()
    this.url = url
    this.reconnectAttempts = reconnectAttempts
    this.backoff = new Backoff({ min: reconnectDelay, max: reconnectDelayMax })
    this.readyState = 'closed'
    this.subs = []
    this.reconnecting = false
    this.skipReconnect = false
    this.connect()
  }

  open(onopen, onerror) {
    if (this.readyState === 'open') {
      return
    }

    this.cleanup()

    logger('opening %s', this.url)
    this.ws = new WebSocket(this.url)
    this.readyState = 'opening'
    this.skipReconnect = false
    const opensub = on(this.ws, 'open', () => {
      this.readyState = 'open'
      onopen()
    })
    const errsub = on(this.ws, 'error', err => {
      this.readyState = 'closed'
      onerror(err)
    })
    const closeSub = on(this.ws, 'close', () => this.onclose())

    this.subs.push(opensub, errsub, closeSub)
  }

  send(message) {
    message = jsonprune(message)
    const valid = validate.message(message)
    if (!valid) {
      logger('trying to send invalid message: %O', message)
      throw new Error('Invalid message format sending to a server')
    }

    if (this.readyState === 'open') {
      this.ws.send(message)
    } else {
      logger('cannot send message, websocket is not open - %o', message)
    }
  }

  connect() {
    this.open(
      () => this.onconnect(),
      err => {
        logger('initial connect error %o', err.message)
        if (!this.reconnecting && this.backoff.attempts === 0) {
          this.reconnect()
        }
      }
    )
  }

  reconnect() {
    const delay = this.backoff.duration()
    logger('reconnect attempt in %d', delay)
    this.reconnecting = true
    const timeout = setTimeout(() => {
      logger('attempting to reconnect')
      this.open(
        () => this.onreconnect(),
        err => {
          logger('reconnect unsuccessful, %o', err.message)
          this.reconnecting = false
          this.reconnect()
        }
      )
    }, delay)

    this.subs.push({ off: () => clearTimeout(timeout) })
  }

  onconnect() {
    logger('websocket opened')
    this.subs.push(
      on(this.ws, 'message', msg => this.onmessage(msg)),
      on(this.ws, 'error', err => this.onerror(err))
    )
    this.emit('connected', true)
    this.emit('connect')
  }

  onreconnect() {
    const { attempts } = this.backoff
    logger('reconnect successful after %d attempts', attempts)
    this.reconnecting = false
    this.backoff.reset()
    this.emit('reconnect')
    this.subs.push(
      on(this.ws, 'message', msg => this.onmessage(msg)),
      on(this.ws, 'close', () => this.onclose()),
      on(this.ws, 'error', err => this.onerror(err))
    )
  }

  onclose() {
    logger('websocket closed')
    this.readyState = 'closed'
    this.emit('connected', false)
    this.backoff.reset()
    this.cleanup()
    if (!this.skipReconnect) {
      this.reconnect()
    }
  }

  onerror(err) {
    logger('websocket error %o', err)
    this.ws.close()
  }

  onmessage(msg) {
    const parsed = jsonparse(msg)
    if (parsed === null) {
      logger('message is not in JSON format')
    } else {
      this.emit('event', parsed)
    }
  }

  disconnect() {
    logger('disconnecting')
    this.skipReconnect = true
    this.reconnecting = true
    if (this.readyState === 'opening') {
      this.cleanup()
    }
    this.backoff.reset()
    this.readyState = 'closed'
    if (this.ws) {
      this.ws.close()
    }
  }

  cleanup() {
    this.subs.forEach(sub => sub.off())
  }
}
