const create = (type, message) =>
  class extends Error {
    constructor(param) {
      super()
      this.type = type
      if (param) {
        this.message = `${message} ${param}`
      } else {
        this.message = message
      }
    }
  }

export const TimeoutError = create(
  'timeout',
  'The request could not be completed in a given time:'
)

export const PermissionError = create(
  'permission-denied',
  'You are trying to access a data you do not have a security permissions to.'
)

export const SubscriptionError = create(
  'subscription-terminated',
  "You don't have permissions to be subscribed to collection:"
)

export const EtagConflictError = create(
  'etag-conflict',
  'Etag does not match when mutating a document.'
)

export const InvalidAuthTokenError = create(
  'invalid-auth-token',
  'The authorization token is invalid.'
)

export const InvalidIdError = create(
  'invalid-id',
  'Only alphanumeric characters, dashes and underscores are allowed. Invalid id:'
)
