import Emitter from 'component-emitter'
import debug from 'debug'
import NodeWebSocket from 'ws'

const BrowserWebSocket = global.WebSocket

const logger = debug('rapid:ws')

export default class WebSocket extends Emitter {
  constructor(url, opts = {}) {
    super()
    this.url = url
    this.perMessageDeflate = opts.perMessageDeflate || false
    this.protocols = opts.protocols || null
    this.isUsingBrowserWebSocket = Boolean(BrowserWebSocket)
    this.open()
  }

  open() {
    const { url, protocols, isUsingBrowserWebSocket } = this
    const wsUrl = `wss://${url}`

    const opts = {
      perMessageDeflate: this.perMessageDeflate
      // more node specific options like SSL etc
    }

    try {
      if (isUsingBrowserWebSocket) {
        this.ws = new BrowserWebSocket(wsUrl)
      } else {
        this.ws = new NodeWebSocket(wsUrl, protocols, opts)
      }
    } catch (err) {
      this.emit('error', err)
    }

    this.addEventListeners()
  }

  addEventListeners() {
    this.ws.onopen = () => this.emit('open')
    this.ws.onmessage = event => this.emit('message', event.data)
    this.ws.onclose = () => this.emit('close')
    this.ws.onerror = err => this.emit('error', err)
  }

  send(message) {
    if (this.ws.readyState !== this.ws.OPEN) {
      throw new Error('WebSocket is not open')
    }

    try {
      this.ws.send(JSON.stringify(message))
    } catch (error) {
      logger(
        'WebSocket was not able to send data, becase it has been already closed.'
      )
    }
  }

  close() {
    if (
      typeof this.ws !== 'undefined' &&
      (this.ws.readyState === this.ws.CONNECTING ||
        this.readyState === this.ws.OPEN)
    ) {
      this.ws.close()
    }
  }

  get CONNECTING() {
    return 0
  }
  get OPEN() {
    return 1
  }
  get CLOSING() {
    return 2
  }
  get CLOSED() {
    return 3
  }
  get readyState() {
    return this.ws.readyState
  }
}
