import shortid from 'shortid'

/**
 * Creates a delete protocol event
 * @param {string} colId collection id
 * @param {string} docId documend id
 * @return {Object} event
 */

const del = (colId, docId, etag) => ({
  del: {
    'evt-id': shortid.generate(),
    'col-id': colId,
    doc: {
      id: docId,
      etag
    }
  }
})

export default del
