export default class Channel {
  constructor(connection, id) {
    this.connection = connection
    this.id = id
  }

  subscribe(onvalue, onerror) {
    const unsubscribe = this.connection.subscribeChannel(
      this.id,
      onvalue,
      onerror
    )
    return { unsubscribe }
  }

  publish(message, callback) {
    const p = this.connection.publishChannel(this.id, message)
    if (!callback) {
      return p
    }
    return p.then(callback, callback)
  }

  unsubscribeAll(callback) {
    const handler = (onsuccess, onerror) =>
      this.connection.removeChannel(this.id, onsuccess, onerror)

    if (callback) {
      return handler(callback, callback)
    }
    return new Promise(handler)
  }
}
