import shortid from 'shortid'

/**
 * Create channel subscription event
 * @param {(String|Object)} chanId channel id
 * @param {String} subId subscription id
 * @returns {Object}
 */
const subCh = ({ chanId, subId }) => ({
  'sub-ch': {
    'evt-id': shortid(),
    'chan-id': chanId,
    'sub-id': subId
  }
})

export default subCh
