/*
 * Timer that invokes function after a period of time
 * also has a reset method
 */

export default class Timer {
  constructor(fn, delay) {
    this.fn = fn
    this.delay = delay
    this.interval = null
    this.start()
  }

  start() {
    if (!this.interval) {
      this.interval = setInterval(this.fn, this.delay)
    }
  }

  stop() {
    clearInterval(this.interval)
    this.interval = null
  }

  reset() {
    if (this.interval) {
      clearInterval(this.interval)
      this.interval = setInterval(this.fn, this.delay)
    }
  }
}
