import shortid from 'shortid'

/**
 *
 * @param {string} colId collection ID
 * @param {string} docId document ID
 * @param {object} body properties to mutate
 * @return {Object} event
 */

const mut = (colId, docId, body, etag) => ({
  mut: {
    'evt-id': shortid.generate(),
    'col-id': colId,
    doc: {
      id: docId,
      etag,
      body
    }
  }
})

export default mut
