import { InvalidIdError } from '../errors'

export default function checkId(id) {
  if (typeof id !== 'string') {
    throw new TypeError(`${id} is not type of string`)
  }

  const re = /^[0-9a-zA-Z_-]+$/
  const valid = re.test(id)

  if (!valid) {
    throw new InvalidIdError(id)
  }

  return true
}
