import shortid from 'shortid'

/**
 * Creates publish event
 * @param {(String|Object)} chanId channel id
 * @param {Object} body channel message
 * @returns {Object}
 */
const pub = ({ chanId, body }) => ({
  pub: {
    'evt-id': shortid(),
    'chan-id': chanId,
    body
  }
})

export default pub
